button {
  padding: 10px 20px;
  background-color: #2a52be; /* Loyal blue */
  color: white;
  border: 1px solid white;
  border-radius: 12px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin: 10px;
}

button:hover {
  background-color: #1f3fa0;
}